
.header{
    @include display-flex-column-center;
    width: 100%;
    background-color: $color-background-primary;
    &__logo{
        @include display-flex-column-center-center;
        height: 65px;
    }

    &__arrow{
        @include display-flex-row-center-center;
        position: absolute;
        top: 18px;
        left: 20px;
        font-size: 24px;
        
        & i{
            color: $color-text;
        }
  

    }
    
}